html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #EAE8E8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
  font-weight: 700;
}

h2 {
  font-size: 3.8rem;
  font-weight: 700;
  line-height: 1;
  margin-top: 5.5rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.inicio {
  background-image: url(../../../build/img/portada.jpg);
}

.inicio {
  background-image: url(../../../build/img/portada.webp);
}

.header {
  background-color: #828282;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 50rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 50rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #000000;
  font-weight: 700;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    color: #FFFFFF;
  }
}
.contenido-header .shell img {
  display: none;
  margin-top: 10rem;
  margin-left: 5rem;
}
@media (min-width: 768px) {
  .contenido-header .shell img {
    display: flex;
    align-items: center;
  }
}
.contenido-header .shell img {
  height: 4rem;
  width: auto;
}

.sombra {
  background-color: rgba(10, 7, 7, 0.611);
}
.sombra p {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.3rem;
  /*@include m.desktop {
      margin-left: 5rem;
      text-align: left;
  } 
  @include m.desktopXL {
      margin-left: 15rem;
      text-align: left;
  } */
}
@media (min-width: 768px) {
  .sombra p {
    font-size: 2rem;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  display: none;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 4rem;
}

@media (min-width: 992px) {
  .nosotros {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.sobre-nosotros {
  text-align: center;
}
.sobre-nosotros img {
  align-items: center center;
  height: 25rem;
}
.sobre-nosotros h2 {
  color: #A50808;
  font-weight: 400;
}
.sobre-nosotros h3 {
  font-size: 1.8rem;
}

html {
  box-sizing: border-box;
  font-size: 80%;
}
html p {
  font-size: 1.2rem;
}

@media (min-width: 768px) {
  .mecanica {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 992px) {
  .mecanica {
    display: grid;
    gap: 1.5rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (min-width: 768px) {
  .electronica,
.control {
    display: grid;
    gap: 6rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 992px) {
  .electronica,
.control {
    margin-left: 10rem;
    margin-right: 10rem;
  }
}

@media (min-width: 768px) {
  .ti {
    display: grid;
    gap: 15rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .ti {
    margin-left: 22rem;
    margin-right: 22rem;
  }
}

@media (min-width: 992px) {
  .profesor {
    margin-left: 40rem;
    margin-right: 40rem;
  }
}

h4 {
  font-size: 1.8rem;
}

.banner {
  position: relative;
}

.banner:hover .equipo-barra {
  height: 13rem;
}

.equipo-barra {
  position: absolute;
  background: #FFFFFF;
  width: auto;
  left: 1.5rem;
  bottom: 0rem;
  right: 1.5rem;
  text-align: center;
  height: 8rem;
  overflow: hidden;
  transition: all 0.2s ease-in-out;
}

.equipo-barra:hover p {
  font-size: 1.2rem;
  position: absolute;
  color: #000000;
  left: 0rem;
  bottom: 0rem;
}

.sesion {
  margin: 4rem;
}
.sesion h2 {
  padding-top: 2rem;
}
@media (min-width: 992px) {
  .sesion {
    display: grid;
    gap: 1;
    grid-template-columns: 73% 27%;
  }
}
.sesion h2 {
  font-size: 4.1rem;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1;
  margin-top: 5.5rem;
}

.imagen-formulario {
  background-position: center center;
}

.formulario {
  background-color: #b3b1b1;
  padding-left: 1.5rem;
  border-radius: 1.4rem;
}
@media (min-width: 992px) {
  .formulario {
    border-radius: 0;
  }
}
.formulario h3 {
  font-size: 2rem;
}
.formulario .input label {
  color: #FFFFFF;
  font-size: 2.2rem;
  font-weight: 700;
  text-align: left;
}
.formulario .password-olvidada a {
  display: inline-block;
  text-align: center;
  margin: 0.5rem 0 3rem 0.5rem;
  font-size: 1.5rem;
}

.input input {
  width: 95%;
  padding: 1rem 1.6rem;
  border-radius: 0.6rem;
  outline: none;
}

.input input[type=submit] {
  background-color: #A50808;
  color: #FFFFFF;
  font-size: 1.9rem;
  font-weight: 700;
  border: none;
  margin-top: 1rem;
  transition: background-color 0.3s ease-in-out;
  padding: 1rem 10.8rem;
  border-radius: 1.5rem;
}

.input input[type=submit]:hover {
  cursor: pointer;
  background-color: #008F39;
}

.footer {
  background-color: #828282;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
.footer p {
  font-size: 2rem;
  padding-bottom: 2rem;
}

.tabla {
  max-width: 100%;
  min-width: 10%;
  margin-top: 8rem;
  margin-bottom: 18rem;
}
.tabla th {
  width: 16%;
  border-collapse: collapse;
  font-size: 0.7rem;
}
.tabla .tabla-datos {
  font-size: 1.35rem;
}
@media (min-width: 768px) {
  .tabla {
    width: 100%;
    margin-bottom: 28rem;
  }
  .tabla .tabla-datos {
    font-size: 2.5rem;
  }
  .tabla th {
    font-size: 1.4rem;
  }
}
@media (min-width: 992px) {
  .tabla {
    margin-bottom: 13rem;
  }
  .tabla .tabla-datos {
    font-size: 3.6rem;
  }
  .tabla th {
    font-size: 1.8rem;
  }
}
@media (min-width: 1200px) {
  .tabla .tabla-datos {
    font-size: 4.5rem;
  }
  .tabla th {
    font-size: 2.4rem;
  }
}
.tabla tbody {
  text-align: center;
  font-family: Lobster;
  background-color: #828282;
  color: #FFFFFF;
}
.tabla .bg-rc {
  background-color: #000000;
  color: #FFFFFF;
  padding: 0.3rem;
  margin: 0rem;
  border: 0rem;
  font-family: "Times New Roman", Times, serif;
}
.tabla .bg-ro {
  background-color: #FFFFFF;
  color: #000000;
  font-family: "Times New Roman", Times, serif;
}