@use 'base/variables' as v;
@use 'base/mixins' as m;

html {
    box-sizing: border-box;
    font-size: 80%;
    p {
        font-size: 1.2rem;
    }
}

.mecanica{
    @include m.tablet {
        @include m.grid(3 , 2rem)
    }
    @include m.desktop {
        @include m.grid(4, 1.5rem)
    }
}
.electronica,
.control{
    @include m.tablet {
        @include m.grid(3 , 6rem)
    }
    @include m.desktop{
        margin-left: 10rem;
        margin-right: 10rem;
    }
}
.ti {
    @include m.tablet {
        @include m.grid(2 , 15rem)
    }
    @include m.desktop{
        margin-left: 22rem;
        margin-right: 22rem;
    }
}
.profesor{
    @include m.desktop{
        margin-left: 40rem;
        margin-right: 40rem;
    }
}
h4 {
    font-size: 1.8rem;
}

.banner {
    position: relative; 
}
.banner:hover .equipo-barra {
      height: 13rem; }
.equipo-barra {
    position: absolute;
    background: v.$blanco;
    width: auto;
    left: 1.5rem;
    bottom: 0rem;
    right: 1.5rem;
    text-align: center;
    //padding: 1.5rem;
    height: 8rem;
    overflow: hidden;
    transition: all .2s ease-in-out; }
    .equipo-barra:hover p {
        font-size: 1.2rem;
        position: absolute;
        color: v.$negro;
        left: 0rem;
        bottom: 0rem;
        //text-align: center;
    }
