@use 'base/variables' as v;
@use 'base/mixins' as m;

@include m.desktop {
    .nosotros{
        @include m.grid(2,4rem)
    }
}

.sobre-nosotros {
    text-align: center;
    img {
        align-items: center center;
        height: 25rem;
    }
    h2{
        color: v.$rojo;
        font-weight: v.$regular;
    }
    h3 {
        font-size: 1.8rem;
    }
}