@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3rem;
    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
    }
}
.navegacion {
    display: none;
    @include m.tablet {
        display: flex;
        align-items: center;
    }
    a{
        color: v.$blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include m.tablet {
            margin-right: 2rem;
            font-size: 2rem;

            &:last-of-type {
                margin-right: 0;
            }
        }
    }
}
.mobile-menu {
    @include m.tablet {
        display: none;
    }
    img {
        width: 4rem;
    }
}
