@use 'base/variables' as v;
@use 'base/mixins' as m;

.inicio {
    background-image: url(../../../build/img/portada.jpg);
}
.inicio {
    background-image: url(../../../build/img/portada.webp);
}

.header {
    background-color: v.$gris;
    padding: 1rem 0 3rem 0;

    &.inicio{
        background-position: center center;
        background-size: cover;
        @include m.tablet {
            min-height: 50rem;
        }
        .contenido-header {
            @include m.tablet {
                min-height: 50rem;
            }
        }
    }
}
.contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1 {
        color: v.$negro;
        font-weight: v.$bold;
        @include m.tablet {
        color: v.$blanco;
        }
    }
    .shell{
        img{
            display: none;
            margin-top: 10rem;
            margin-left: 5rem;

        }
        @include m.tablet {
            img {
                display: flex;
                align-items: center;
            }
        }
        
        img {
            height: 4rem;
            width: auto;
        }

    }
    
}
.sombra{
    background-color: v.$sombra;
    
    p {
        color: v.$blanco;
        text-align: center;
        font-size: 1.3rem;
        @include m.tablet {
            font-size: 2rem;
        }
        /*@include m.desktop {
            margin-left: 5rem;
            text-align: left;
        } 
        @include m.desktopXL {
            margin-left: 15rem;
            text-align: left;
        } */
    }
}

