@use 'base/variables' as v;
@use 'base/mixins' as m;
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');


.tabla{
    max-width: 100%;
    min-width: 10%;
    margin-top: 8rem;
    margin-bottom: 18rem;
    th{
        width: 16%;
        border-collapse: collapse;
        font-size: .7rem;
    }
    .tabla-datos{
        font-size: 1.35rem;
    }
    @include m.tablet {
        width: 100%;
        margin-bottom: 28rem;
        .tabla-datos{
            font-size: 2.5rem;
        }
        th{
            font-size: 1.4rem;
        }
    }
    @include m.desktop {
        margin-bottom: 13rem;
        .tabla-datos{
            font-size: 3.6rem;
        }
        th{
            font-size: 1.8rem;
        }
    }
    @include m.desktopXL {
        .tabla-datos{
            font-size: 4.5rem;
        }
        th{
            font-size: 2.4rem;
        }
    }
    
    tbody{
        text-align:  center;
        font-family: Lobster;
        background-color: v.$gris;
        color: v.$blanco;

    }
    .bg-rc{
        background-color: v.$negro;
        color: v.$blanco;
        padding: 0.3rem;
        margin: 0rem;
        border: 0rem;
        font-family: 'Times New Roman', Times, serif;
    }
    .bg-ro{
        background-color: v.$blanco;
        color: v.$negro;
        font-family: 'Times New Roman', Times, serif;
    }
}
