@use 'base/variables' as v;
@use 'base/mixins' as m;
.sesion{
    margin: 4rem;
    h2{
        
        padding-top: 2rem;
    }
    @include m.desktop {
        display: grid;
        gap: 1;
        grid-template-columns: 73% 27%;
    }
    h2 {
        font-size: 4.1rem;
        color: v.$blanco;
        font-weight: v.$bold;
        line-height: 1;
        margin-top: 5.5rem;
    }
}
.imagen-formulario{
    background-position: center center;
    
}
.formulario {
    background-color: v.$grisMedio;
    padding-left: 1.5rem;
    border-radius: 1.4rem;
    @include m.desktop {
        border-radius: 0;
    }
    //text-align: center;
    h3 {
        font-size: 2rem;
    }
    .input label {
        color: v.$blanco;
        font-size: 2.2rem;
        font-weight: v.$bold;
        text-align: left;
    }
    .password-olvidada a{
        display: inline-block;
        text-align: center;
        margin: 0.5rem 0 3rem 0.5rem;
        font-size: 1.5rem;
    }
}
.input input{
    width: 95%;
    padding: 1rem 1.6rem;
    border-radius: 0.6rem;
    outline: none;
}
.input input[type="submit"]{
    background-color: v.$rojo;
    color: v.$blanco;
    font-size: 1.9rem;
    font-weight: v.$bold;
    border: none;
    margin-top: 1rem;
    transition: background-color .3s ease-in-out;
    padding: 1rem 10.8rem;
    border-radius: 1.5rem;
}
.input input[type="submit"]:hover{
  
    cursor: pointer;
    background-color: v.$verde;
}
